* {
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  text-align: center;
  grid-template-rows: repeat(2, 231px);
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 40px 30px;
  margin: 30px 55px;
  display: grid;
}

@media screen and (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.people__card {
  box-sizing: border-box;
  width: 100%;
  min-height: 264px;
  transition: transform 1s ease-in-out;
}

.people__card:hover {
  z-index: 10;
  background-color: #eee;
  border-radius: 12px;
  position: relative;
  transform: scale(1.2);
}

.people__card:hover .photo {
  box-sizing: border-box;
  z-index: 1;
  padding: 12px;
}

.people__card:hover .photo img {
  border-radius: 60px;
}

.people__card:hover .photo .rank {
  right: 27px;
}

.people__card:hover .extra {
  display: block;
}

.photo {
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
  position: relative;
}

.photo img {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.name {
  color: #2f4f4f;
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 700;
}

.extra {
  color: #000;
  font-size: 21.6px;
  font-weight: 400;
  display: none;
}

.pay:before {
  content: "$";
}
